@import url("https://fonts.googleapis.com/css2?family=Montserrat&display=swap");

* {
	font-family: "Montserrat", sans-serif;
}

.App {
	max-width: 400px;
	max-height: 400px;
	margin: 0 auto;
	text-align: center;
}

.button {
	cursor: pointer;
	transition: all 0.3s;
}

.button:hover {
	transform: scale(1.05);
}

.button:active {
	transform: scale(1);
}

.Profile {
	margin-top: 40px;
}

h1 {
	font-size: 20px;
}
